
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* text-align: center; */
  color: #2c3e50;
  margin: 6rem 0rem 0rem 0rem;
}
.container{
    margin: 10rem 0rem 5rem 0rem;
    float: middle;
}
.icon {
    width: 1.2em;
    height: 1.2em;
    vertical-align: -0.15em;
    /* fill: currentColor;  */
    fill: rgb(190, 190, 190);
    overflow: hidden;
    display: inline;
    margin: 0.6rem 0.7rem 0.2rem 0.1rem;
    position:relative;
    float:left;
}
a {
    color: #000000;
}
.nav {
    /* added for fixed header*/
    display:block;

    /* position: relative; */
    margin: 0rem 1rem 0rem 1rem;
    padding-top:2rem;
    /* height: 10rem; */
    position: fixed;
    margin:auto;
    top:0rem;
    /* background-color: #ffffff; */
    /* width: 96vw;  */
    width: 100vw;
    z-index: 1;
}
body{
  margin:0;
  padding:0;
}
.nav .nav-title {
    position: absolute;
    left: 18rem;
    /* float: left; */
    height: 3rem;
    font-family: optima;
    vertical-align: middle;
    /* font-family: 'Open Sans', sans-serif; */
    /* font-family: 'Rosario', sans-serif; */
    /* font-family: 'Arsenal', sans-serif;  */
    line-height: normal;
    font-size: 32px;
    color: #202020;
}
.nav >ul{
    list-style-type:none;
    float: right;
    right: 0;
    margin-right: 18rem;
    font-weight: normal;
    font-family: 'Lato', sans-serif;
    font-size: 14px;
    color: #000000;
}
.nav >ul >li {
    margin: 0 2rem;
    text-decoration: none;
    list-style: none;
    display: inline;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    font-size: 13px;
    color:#202020;
    font-family: 'Lato', sans-serif;
    letter-spacing:1.6px;
}
.nav >ul >li:hover {
    color: #959595;
    cursor: pointer;
    /* font-weight: 900; */
    -webkit-transition: 0.2s;
    transition: 0.2s;
}
.copyright{
    float:center;
    padding: 4rem 0;
    text-align: center;
    font-size: 12px;
    font-family: Myriadpro;
    color: #9b9b9b; 
    margin-top: 1rem;
}
.image-middle-container{
    text-align: center;
}
figcaption{
    font-weight: 400;
    font-size: 14px;
    line-height: 1.4;
    color: rgba(0,0,0,.68);
    letter-spacing: 0;
    text-align: center;
    font-family: 'Lato', sans-serif;
}
blockquote{
    font-size: 24px;
    line-height: 1.6;
    letter-spacing: 0.006em;
    font-weight: 400;
    font-style: italic;
    font-family: 'Lato', sans-serif;
    color: rgba(0,0,0,.68);
}
@media (max-width: 40em)  {
  /* Pull out the header and footer */
#app {
  margin: 2rem 0rem 0rem 0rem;
}
.nav{
    margin:0;
    position: relative;
    top:0rem;
    padding-top:1rem;
}
.nav .nav-title {
    left: 0rem;
    font-size: 30px;
}
.nav >ul{
    list-style-type:none;
    right: 0rem;
    margin-right: 0rem;
}
.nav >ul >li {
    margin: 0 0.5rem;
}
.copyright {
    text-align: center;
    margin: 0 auto;
    bottom: 0;
    float:center;
    padding: 4rem 0; 
    margin-top: 1rem;
}
  /* Start the vertical centering */
.image-middle-container {
    vertical-align: middle;
}
  /* Handle the widths */
.nav {
    width: 100%; /* Must be percentage or pixels for horizontal alignment */
}
}
@media (max-width: 70em)and (min-width: 40em) {
  /* Pull out the header and footer */
.nav{
      margin:auto;
}
.nav .nav-title {
    left: 5rem;
}
.nav >ul{
    list-style-type:none;
    right: 0rem;
    margin-right: 5rem;
}
.copyright {
    text-align: center;
    margin: 0 auto;
    bottom: 0;
}
  /* Start the vertical centering */
.image-middle-container {
    vertical-align: middle;
}
  /* Handle the widths */
.nav {
    width: 100%; /* Must be percentage or pixels for horizontal alignment */
}
}
